<template>


  <div>
    
    <Page v-if="pageContent" @show="showPageContent" ></Page>
    <div v-if="showContent">
    <PageHeader></PageHeader>
    <PageMain></PageMain>
    <About @showEdu="showEDU" @showCeti="Cetif"></About>
    <Skills></Skills>
    <Contact></Contact>
    <Footer></Footer>
    </div>

    <Cetification v-if="showC" @home="showPageContent"></Cetification>
    <Education v-if="showE"  @home="showPageContent"></Education>


    <div class="scroll" v-if="showContent">
        <a href="#header"  align="center" style="color: #566573;">
            <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" fill="currentColor" class="bi bi-chevron-double-up" viewBox="0 0 16 16">
